<template>
  <div>
    <v-dialog max-width="500" v-model="addToPlaylist">
      <v-card class="br20">
        <v-card-title class="headline">Add to Playlist</v-card-title>
        <v-card-text>
          <v-combobox
            :items="playlists"
            :color="darkMode ? 'white' : 'primary'"
            label="Select playlist"
            item-text="name"
            v-model="selectedPlaylist"
            return-object
          ></v-combobox>
        </v-card-text>
        <v-card-actions>
          <v-btn :loading="loading" :color="darkMode ? '' : 'primary'" @click="add" round>Add</v-btn>
          <v-btn flat @click="addToPlaylist = false">cancel</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { eventBus } from '@/main'
import storageService from '@/services/blockstack-storage'
export default {
  data: () => ({
    addToPlaylist: false,
    selectedPlaylist: null,
    loading: false
  }),
  computed: {
    ...mapGetters({
      playlists: 'getPlaylists',
      darkMode: 'darkMode'
    })
  },
  mounted () {
    eventBus.$on('addToPlaylist', options => {
      this.content = options.data
      this.type = options.type
      this.addToPlaylist = true
    })
  },
  methods: {
    add () {
      this.loading = true
      let found = null
      let playlists = [...this.playlists]
      let index = playlists.indexOf(this.selectedPlaylist)
      if (this.type) {
        found = playlists[index][this.type].find((item) => item.id === this.content.id)
      }
      if (!found) {
        this.selectedPlaylist[this.type].push(this.content)
        playlists[index] = this.selectedPlaylist
      } else {
        this.loading = false
        this.addToPlaylist = false
        this.selectedPlaylist = null
        return
      }
      this.$store.commit('MUTATION_SET_PLAYLISTS', playlists)
      storageService.putFile({
        fileName: 'my_playlist.json',
        data: playlists,
        options: { encrypt: false }
      }).then(() => {
        this.loading = false
        this.addToPlaylist = false
        this.selectedPlaylist = null
      })
    }
  }
}
</script>

